import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import { fetchCreateFolder } from '../../actions/api/folders';
import { fetchCreateProject } from '../../actions/api/projects';

import { saveProject, setFirstTime } from '../../actions/welcome';
import { notifyError } from '../../actions/notification';
import PrimaryButton from '../lib/Button/PrimaryButton';
import Form from '../lib/Form';
import InputText from '../lib/WelcomeTextField';
import { DEFAULT_FOLDER_NAME } from '../../constants/api/folders';

const styles = {
  root: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
  },
  button: {
    display: 'block',
  },
};

class ProjectForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  handleSubmit(event) {
    event.preventDefault();
    const name = this.state.value.trim();
    if (name.length > 0) {
      this.props.setFirstTime(true);
      this.props.fetchCreateFolder({ name: DEFAULT_FOLDER_NAME })
        .then((response) => {
          const folderId = response.body.id;
          this.props.fetchCreateProject({ name, folder_id: folderId })
            .then((payload) => {
              this.props.saveProject(payload.body.id, payload.body.name);
            });
        });
    } else {
      this.props.notifyError('You have to select a name for your first project');
    }
  }

  render() {
    const { classes } = this.props;
    return (
      <Form onSubmit={this.handleSubmit} className={classes.root}>
        <InputText
          type="text"
          value={this.state.value}
          onChange={this.handleChange}
          placeholder="Write your project name here......"
          autoFocus
          style={{ outlineWidth: 0 }}
        />
        <div className={classes.button}>
          <PrimaryButton type="submit">

            ADD
          </PrimaryButton>
        </div>
      </Form>
    );
  }
}

ProjectForm.propTypes = {
  fetchCreateProject: PropTypes.func.isRequired,
  fetchCreateFolder: PropTypes.func.isRequired,
  saveProject: PropTypes.func.isRequired,
  setFirstTime: PropTypes.func.isRequired,
  classes: PropTypes.any.isRequired,
  notifyError: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  return {
    folderName: state.welcome.folderName,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchCreateProject,
    fetchCreateFolder,
    saveProject,
    setFirstTime,
    notifyError,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ProjectForm));
