import {
  FETCH_GET_PROJECT,
  FETCH_GET_ALL_PROJECTS,
  FETCH_CREATE_PROJECT,
  FETCH_DELETE_PROJECT,
  FETCH_UPDATE_PROJECT,
  FETCH_UPDATE_PROJECT_ORDER,
  FETCH_UPDATE_PROJECT_FAVORITE,
  FETCH_GITHUB_CONNECT,
  FETCH_GITHUB_CONFIRMATION,
  FETCH_GITHUB_REPO,
  FETCH_GITHUB_REMOVE,
  FETCH_PROJECT_REPO,
  FETCH_PROJECT_REPO_ASSIGN,
  FETCH_PROJECT_REPO_UNASSIGN,
  FETCH_TASKS_PROJECT_BETWEEN_DATES,
} from '../../constants/api/projects';

export const fetchGetProject = id => ({
  type: FETCH_GET_PROJECT,
  fetch: true,
  url: `/projects/${id}/`,
  params: {
    method: 'GET',
  },
});

export const fetchGetProjects = () => ({
  type: FETCH_GET_ALL_PROJECTS,
  fetch: true,
  url: '/projects/',
  params: {
    method: 'GET',
  },
});

export const fetchCreateProject = body => ({
  type: FETCH_CREATE_PROJECT,
  fetch: true,
  url: '/projects/',
  params: {
    method: 'POST',
    body,
  },
});

export const fetchDeleteProject = id => ({
  type: FETCH_DELETE_PROJECT,
  fetch: true,
  url: `/projects/${id}/`,
  params: {
    method: 'DELETE',
  },
});

export const fetchUpdateProject = (id, body) => ({
  type: FETCH_UPDATE_PROJECT,
  fetch: true,
  url: `/projects/${id}/`,
  params: {
    method: 'PUT',
    body,
  },
});

