export const fetchGetTasksProject = (id, body) => ({
  type: FETCH_TASKS_PROJECT_BETWEEN_DATES,
  fetch: true,
  url: `/projects/${id}/workload`,
  params: {
    method: 'GET',
  },
  queryParams: {
    ...body,
  },
});
