import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import CircularProgress from '../../components/lib/CircularProgress';
import WelcomeContainer from '../../components/welcome';
import { fetchGetProjects } from '../../actions/api/projects';
import { fetchGetFolders } from '../../actions/api/folders';
import { fetchedProjects } from '../../selectors/projects';

class Welcome extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }

  async componentWillMount() {
    this.props.fetchGetFolders();
    if (!this.props.didFetchProjects) {
      this.props.fetchGetProjects()
        .then((response) => {
          if (response.ok) {
            this.setState({ loading: false });
          }
        });
    } else {
      this.setState({ loading: false });
    }
  }

  render() {
    const { firstTime, didFetchProjects } = this.props;
    if (this.state.loading) {
      return <CircularProgress />;
    }
    if (didFetchProjects && !firstTime) {
      return <Redirect to="/home" />;
    }
    return <WelcomeContainer />;
  }
}

Welcome.propTypes = {
  fetchGetProjects: PropTypes.func.isRequired,
  didFetchProjects: PropTypes.bool.isRequired,
  fetchGetFolders: PropTypes.func.isRequired,
  firstTime: PropTypes.bool.isRequired,
};

function mapStateToProps(state, ownProps) {
  return {
    didFetchProjects: fetchedProjects(ownProps, state),
    firstTime: state.welcome.firstTime,
  };
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchGetProjects,
    fetchGetFolders,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Welcome);
