import {
  FETCH_GET_FOLDER,
  FETCH_GET_ALL_FOLDERS,
  FETCH_CREATE_FOLDER,
  FETCH_DELETE_FOLDER,
  FETCH_UPDATE_FOLDER,
} from '../../constants/api/folders';

export const fetchGetFolders = () => ({
  type: FETCH_GET_ALL_FOLDERS,
  fetch: true,
  url: '/folders/',
  params: {
    method: 'GET',
  },
});

export const fetchGetFolder = id => ({
  type: FETCH_GET_FOLDER,
  fetch: true,
  url: `/folder/${id}/`,
  params: {
    method: 'GET',
  },
});

export const fetchCreateFolder = body => ({
  type: FETCH_CREATE_FOLDER,
  fetch: true,
  url: '/folders/',
  params: {
    method: 'POST',
    body,
  },
});

export const fetchDeleteFolder = id => ({
  type: FETCH_DELETE_FOLDER,
  fetch: true,
  url: `/folders/${id}/`,
  params: {
    method: 'DELETE',
  },
});

export const fetchUpdateFolder = (id, body) => ({
  type: FETCH_UPDATE_FOLDER,
  fetch: true,
  url: `/folders/${id}/`,
  params: {
    method: 'PUT',
    body,
  },
});
