import {
  SAVE_FOLDER,
  SAVE_PROJECT,
  SET_FIRST_TIME,
} from '../constants/welcome';

export const saveFolder = name => ({
  type: SAVE_FOLDER,
  payload: {
    body: {
      name,
    },
  },
});

export const saveProject = (id, name) => ({
  type: SAVE_PROJECT,
  payload: {
    body: {
      id,
      name,
    },
  },
});

export const setFirstTime = bool => ({
  type: SET_FIRST_TIME,
  payload: {
    body: {
      firstTime: bool,
    },
  },
});
