import React, { Fragment, Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import FolderIcon from '@material-ui/icons/Folder';
import Background from '../../assets/background.png';

import ProjectForm from './ProjectForm';
import PrimaryButton from '../lib/Button/PrimaryButton';

const styles = {
  root: {
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundImage: `url(${Background})`,
    flex: 1,
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  welcome: {
    marginBottom: 20,
    color: 'white',
    fontSize: 20,
  },
  subtitle: {
    marginBottom: 30,
    color: 'white',
    fontSize: 20,
  },
  title: {
    marginBottom: 60,
    color: 'white',
    fontSize: 50,
    fontWeight: 'bold',
    alignItems: 'felx-end',
  },
  logo: {
    color: '#ea5941',
    fontSize: 80,
    marginBottom: -13,
    marginRight: 10,
  },
  form: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
  },
  content: {
    margin: '20px 0px',
    color: '#ea5941',
    fontWeight: 'bold',
    fontSize: 16,
    marginBottom: 60,
  },
};

class WelcomeContainer extends Component {
  constructor(props) {
    super(props);
    this.state = { redirect: false };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    this.setState({ redirect: true });
  }

  render() {
    const {
      classes,
      project,
      projectName,
    } = this.props;
    if (this.state.redirect) {
      return (<Redirect to="/home" />);
    }
    return (
      <Fragment>
        <div className={classes.root}>
          <Typography className={classes.welcome}>

            Welcome to
          </Typography>
          <Typography className={classes.title}>
            <FolderIcon className={classes.logo} />

            ZmartBoard
          </Typography>
          {
            !project
            && (
              <div className={classes.form}>
                <Typography className={classes.subtitle}>

                  Let&apos;s create your first &nbsp;
                  <b>

                    &quot;Project&quot;
                  </b>
                </Typography>
                <ProjectForm />
              </div>
            )
          }
          {
            project
            && (
              <div className={classes.form}>
                <Typography className={classes.subtitle}>

                  Nice!&nbsp;
                  <b>

                    &quot;{projectName}&quot;&nbsp;
                  </b>

                  has been created&nbsp;
                </Typography>
                <div style={{ display: 'block' }}>
                  <PrimaryButton type="button" onClick={this.handleClick}>

                    LET&apos;S GET STARTED
                  </PrimaryButton>
                </div>
              </div>
            )
          }
        </div>
      </Fragment>
    );
  }
}

WelcomeContainer.propTypes = {
  project: PropTypes.bool.isRequired,
  classes: PropTypes.any.isRequired,
  projectName: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    project: state.welcome.project,
    projectName: state.welcome.projectName,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(WelcomeContainer));
